import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

// import { AuthXService } from '../../../core/services/authx.service';
import { AuthService } from '../../../core/services/auth.service';

import { ActivatedRoute, Router } from '@angular/router';
import { first } from 'rxjs/operators';

// import { environment } from '../../../../environments/environment';

@Component({
   selector: 'app-login',
   templateUrl: './login.component.html',
   styleUrls: ['./login.component.scss']
})

/**
 * Login component
 */
export class LoginComponent implements OnInit {

   loginForm: FormGroup;
   submitted = false;
   error = '';
   returnUrl: string;

   // set the currenr year
   year: number = new Date().getFullYear();

   // tslint:disable-next-line: max-line-length
   constructor(private formBuilder: FormBuilder, private route: ActivatedRoute, private router: Router,
      // private AuthXService: AuthXService,
      private authFackservice: AuthService) { }

   ngOnInit() {
      this.loginForm = this.formBuilder.group({
         username: ['', [Validators.required]],
         password: ['', [Validators.required]],
      });

      // reset login status
      // this.AuthXService.logout();
      // get return url from route parameters or default to '/'
      // tslint:disable-next-line: no-string-literal
      this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
   }

   // convenience getter for easy access to form fields
   get f() { return this.loginForm.controls; }

   /**
    * Form submit
    */
   onSubmit() {
      this.submitted = true;

      // stop here if form is invalid
      if (this.loginForm.invalid) {
         return;
      } else {
         // if (environment.defaultauth === 'firebase') {
         //    this.AuthXService.login(this.f.username.value, this.f.password.value).then((res: any) => {
         //       this.router.navigate(['/dashboard']);
         //    })
         //       .catch(error => {
         //          this.error = error ? error : '';
         //       });
         // } else {
         this.authFackservice.login(this.loginForm.value)
            .pipe(first())
            .subscribe(
               data => {
                  this.router.navigate(['']);
               },
               error => {
                  this.error = error ? error : '';
               });
         // }
      }
   }
}
